.layout{
  position :absolute;
  top :0;
  bottom: 0;
  left :0;
  width : 100%;
  height : 100%;
}

.navbar {
  align-items: flex-start;
}
.navbar .text-light{
  text-align: left;
  padding-top : 10px;
  color :#f8f9fa9e !important;
}
.menu-toggler{
  position: relative;
  width : 50px;
  height :50px;
  cursor: pointer;
  box-sizing: border-box;
  z-index: 1;
}
.menu-toggler span{
  position: absolute;
  width : 25px;
  height: 1px;
  background: #fff;
  top :calc(50% - 1px);
  left :calc(50% - 15px);
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
.menu-toggler span:nth-child(1){
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
.menu-toggler.active span:nth-child(1){
  height: 2px;
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.menu-toggler.active span:nth-child(2){
  height: 2px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.nav-menu{
  display : flex;
  padding: 0 120px;
}
.left-menu{
  flex : 1 1;
}
.right-menu{
  flex : 1 1;
}
.menu{
  text-align: right;
}
.menu-items{
  display : flex;
  cursor: pointer;
}
.menu-item-title{
  flex : 1 1;
  font: 600 12vw/1.25 'Lato', sans-serif;
  letter-spacing: -8px;
  -webkit-transition: -webkit-transform .7s cubic-bezier(.19,1,.22,1);
  transition: -webkit-transform .7s cubic-bezier(.19,1,.22,1);
  transition: transform .7s cubic-bezier(.19,1,.22,1);
  transition: transform .7s cubic-bezier(.19,1,.22,1), -webkit-transform .7s cubic-bezier(.19,1,.22,1);
  transition: transform .7s cubic-bezier(.19,1,.22,1),-webkit-transform .7s cubic-bezier(.19,1,.22,1);
  color: #fff;
  opacity: 0.3;
  padding: 0 65px 0 50px;
}
.menu-item-title:hover{
  opacity :1;
  -webkit-transform: translateX(-10%);
          transform: translateX(-10%);
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.menu-item-no{
  align-self: flex-end;
  color: #fff;
  opacity: 0.3;
  padding : 30px;
}
.card{
  background: #000;
}
.navbar-wrapper{
  width : 100%;
  display :flex;
  position: relative;
  z-index: 222;
}
.collapse-wrapper{
  width :100%;
  position :fixed;
  top:0;
  left: 0;
}
